import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController } from 'ionic-angular';
import { File } from '@ionic-native/file';
import { Utils } from '../../services/util.service';
import { TranslateService } from '@ngx-translate/core/src/translate.service';

/**
 * Generated class for the ManualPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-manual',
  templateUrl: 'manual.html',
})
export class ManualPage {
  pdfSrc: string = '/assets/doc/SmartTestDriveManual.pdf';
  loading: any;
  loadingMsg: string;

  manual: any;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private file: File,
    private utils: Utils,
    private loadingCtrl: LoadingController,
    private translateService: TranslateService
  ) {
    this.translateService.get('msg.loading').subscribe((value) => {
      this.loadingMsg = value;
    })
  }

  ionViewDidLoad() {
    if (!this.utils.isBrowser()) {
      this.loadPdfBrowser();
    }

    console.log('ionViewDidLoad ManualPage');
  }
  openManual(idx) {
    alert(idx + 1 + "번째 manual open")
  }

  async loadPdf(path: string) {
    return new Promise<string>((resolve, reject) => {
  
      const request = new XMLHttpRequest();
      request.open('GET', path, true);
      request.responseType = 'blob';
  
      request.onload = () => {
        const reader = new FileReader();
  
        reader.onload = (e: any) => {
          this.loading.dismiss();
          resolve(e.target.result);
        }
        reader.onerror = err => {
          this.loading.dismiss();
          reject(err);
        } 
        reader.readAsDataURL(request.response);
      };
  
      request.send();
    });
  }

  async loadPdfBrowser() {
    this.loading = this.loadingCtrl.create({
      content: this.loadingMsg
    });
    this.loading.present();

    this.pdfSrc = await this.loadPdf(this.file.applicationDirectory + 'www' + this.pdfSrc);
  }
}



